                              <nav class="menu">
                                <ul>
                                  <li @click="lang=0; switchLang('uz')" :style="lang==1 ? 'display:block;':'display:none;'" class="">UZ</li>
                                  <li @click="lang=0; switchLang('ru')" :style="lang==1 ? 'display:block;':'display:none;'" class="">RU</li>
                                  <li @click="lang=0; switchLang('en')" :style="lang==1 ? 'display:block;':'display:none;'" class="">EN</li>
