<script setup>
import {useI18n} from "vue-i18n";
let { locale } = useI18n({useScope: 'global'})
const switchLang = (lang) =>{
  locale.value = lang
  localStorage.setItem('lang', locale.value)
}
</script>

<script>
export default  {
  name: "AppNavbar",
  data(){
    return{
      count:0,
      lang: 0
    }
  },
  methods:{
    getImgUrl(pic){
      return require('../img/logo/'+pic)
    },
    toggle(){
      if(this.lang==0){
        this.lang=1
      }
      else {
        this.lang=0
      }
    },
    toggleClass(){
        document.body.classList.toggle('mobile-menu-visible')
    }
  }
}
</script>

<style scoped>
form{
}
  .btn {
