<template>
  <section id="services" class="ucm-area ucm-bg" data-background="img/bg/ucm_bg.jpg">
    <div class="ucm-bg-shape" data-background="img/bg/ucm_bg_shape.png"></div>
    <div class="container">
      <div class="row justify-content-center align-items-end mb-55">
        <div class="col-lg-6">
          <div class="section-title text-center">
            <h2 class="title">{{ $t('services.services_title') }}</h2>
          </div>
        </div>
      </div>
      <div class="tab-content" id="myTabContent">
        <div class="tab-pane fade show active" id="tvShow" role="tabpanel" aria-labelledby="tvShow-tab">
          <div class="ucm-active owl-carousel">
            <div class="row">
                <movie-item v-for="post in posts" :action="'serivices'" :key="post" :items="post"></movie-item>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import {useI18n} from "vue-i18n";
useI18n({useScope: 'global'})
</script>
<script>
import MovieItem from "@/components/ui/MovieItem.vue";
import axios from "axios";

export default {
  name: "AppUcmarea",
  data(){
    return {
      posts: [],
      lang: localStorage.getItem('lang')
    }
  },
  mounted () {
    axios
        .get(`https://gofra-mahsulotlari.com/api/services?lang=${this.lang}`)
        .then((response) => {
          this.posts = response.data

        })
  },
  components: {MovieItem}
}
</script>
<style scoped>

.ucm-bg{
  background-image: url("../img/bg/tv_series_bg.jpg");
}
.ucm-bg-shape{
  background-image: url("../img/bg/ucm_bg_shape.png");
}
.nav-tabs .nav-link.active {
  background-color: #20212b;
}

</style>