<script>
import axios from 'axios'
export default {
  data(){
    return {
      info: '',
      img:'',
      lang: localStorage.getItem('lang')
    }
  },
  methods:{
    getImgUrl(pic){
      return require('../img/logo/'+pic)
    }
  },
  mounted () {
    axios
        .get(`https://gofra-mahsulotlari.com/api/slider?lang=${this.lang}`)
        .then(response => {
          this.info = response.data.content
          this.img = response.data.image
        })
  }
}
</script>

