<template>
  <section id="samples" class="top-rated-movie tr-movie-bg" data-background="img/bg/tr_movies_bg.jpg">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-lg-8">
          <div class="section-title text-center mb-50">
            <h2 class="title">{{$t('samples.sample_title')}}</h2>
          </div>
        </div>
      </div>
      <div class="row tr-movie-active">
        <movie-item v-for="post in posts" :action="'products'" :key="post" :items="post"></movie-item>
      </div>
    </div>
  </section>
</template>
<script setup>
import {useI18n} from "vue-i18n";
useI18n({useScope: 'global'})
</script>
<script>
import MovieItem from "@/components/ui/MovieItem.vue";
import axios from "axios";

export default {
  name: "AppRated",
  components: {MovieItem},
  data(){
    return{
      posts:[],
      lang: localStorage.getItem('lang')
    }
  },
  mounted () {
    axios.get(`https://gofra-mahsulotlari.com/api/products?lang=${this.lang}`)
        .then((response) => {
          this.posts = response.data.data
        })
  }
}
</script>

<style scoped>
.tr-movie-bg{
  background-image: url(../img/bg/tv_series_bg.jpg);
}
</style>