<template>
  <button @click="scrollToTop" ref="appButton" class="app-button" @focus="count=1">
    <font-awesome-icon icon="fa-solid fa-angle-up" style="color: black;" :style="count==1 ? 'color:white;':'color:black;'"/>
  </button>
</template>

<script>
import { onMounted, ref, onBeforeMount } from "vue";
export default {
  name: "AppButton",
  data(){
    return{
      count:0
    }
  },
  setup() {
    const appButton = ref();
    const userScroll = () => {
      if (window.scrollY > 0) {
        appButton.value.classList.add("showButton");
      } else {
        appButton.value.classList.remove("showButton");
      }
    };
    onMounted(() => {
      window.addEventListener("scroll", userScroll);
    });
    onBeforeMount(() => {
      window.removeEventListener("scroll", userScroll);
    });
    const scrollToTop = () => {
      window.scrollTo({ top: 0, behavior: "smooth" });
    };
    return { appButton, scrollToTop };
  },
};
</script>

<style scoped>
.app-button {
  border-radius: 50%;
  height: 50px;
  width: 50px;
  padding: 4px;
  border: none;
  cursor: pointer;
  position: fixed;
  bottom: 40px;
  display: none;
  right: 40px;
  background: #e4d804;
  z-index: 10000;
  transition: 1s ease;
}
.app-button:hover{
  background: #171d22;
}
.app-button:hover fa_icon{
  color: white;
}
.showButton {
  display: block;
}
</style>