<template>
  <div class="container-fluid app-team-bg">
    <div class="container" style="padding: 100px 0;">
      <div class="row justify-content-center">
        <div class="col-lg-8">
          <div class="section-title text-center mb-50">
            <h2 class="title">{{ $t('our_team.title') }}</h2>
          </div>
        </div>
      </div>
      <div class="h-full">
        <ul class="carousel">
          <swiper
              :slides-per-view="3"
              :space-between="50"
              @swiper="onSwiper"
              @slideChange="onSlideChange"
              :navigation="true"
              :breakpoints="breakpoints"
              class="h-full"
          >
            <swiper-slide v-for="item in team" :key="item">
              <li class="card">
                <div class="img"><img :src="'https://gofra-mahsulotlari.com/storage/uploads/'+action+'/'+item.image" alt="img" draggable="false"></div>
                <h2>{{ item.name }}</h2>
                <span>{{ item.position }}</span>
              </li>
            </swiper-slide>
          </swiper>
        </ul>
      </div>
    </div>
  </div>

</template>
<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// import Swiper core and required modules
import SwiperCore, {Pagination, Navigation} from "swiper";
// swiper bundle styles
import "swiper/swiper-bundle.min.css";

// swiper core styles
import "swiper/swiper.min.css";
import axios from "axios";

// modules styles
SwiperCore.use([Pagination, Navigation]);

export default {
  data(){
    return{
      action: 'leaderships',
      lang: localStorage.getItem('lang'),
      team:[],
      breakpoints: {
        // when window width is >= 320px
        300: {
          slidesPerView: 1,
          spaceBetween: 0,
        },
        650: {
            slidesPerView: 2,
            spaceBetween: 20,
        },
        900: {
          slidesPerView: 3,
          spaceBetween: 20,
        },
        1350: {
          slidesPerView: 4,
          spaceBetween: 20,
        },
      }

    }
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    const onSwiper = (swiper) => {
      console.log(swiper);
    };
    const onSlideChange = () => {
      console.log("slide change");
    };
    return {
      onSwiper,
      onSlideChange,
    };
  },
  mounted () {
    axios.get(`https://gofra-mahsulotlari.com/api/leaderships?lang=${this.lang}`)
        .then((response) => {
          this.team = response.data
        })
  }
};
</script>
<style scoped>
.wrapper {
  max-width: 1100px;
  width: 100%;
  position: relative;
}
h2{
  color: #fff;
}
.app-team-bg{
  background-image: url(../img/bg/services_bg.jpg);
}
.wrapper .carousel{
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: calc((100% / 3) - 12px);
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  gap: 16px;
  border-radius: 8px;
  scroll-behavior: smooth;
  scrollbar-width: none;
}
.carousel::-webkit-scrollbar {
  display: none;
}
.carousel.no-transition {
  scroll-behavior: auto;
}
.carousel.dragging {
  scroll-snap-type: none;
  scroll-behavior: auto;
}
.carousel.dragging .card {
  cursor: grab;
  user-select: none;
}
.carousel :where(.card, .img) {
  display: flex;
  justify-content: center;
  align-items: center;
}
.carousel .card {
  scroll-snap-align: start;
  height: 342px;
  list-style: none;
  background: #12111c;
  cursor: pointer;
  padding-bottom: 15px;
  flex-direction: column;
  border-radius: 8px;
}
.carousel .card .img {
  background: #2b2b2c;
  height: 148px;
  width: 148px;
  border-radius: 50%;
}
.card .img img {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #fff;
}
.carousel .card h2 {
  font-weight: 500;
  font-size: 1.20rem;
  text-align: center;
  margin: 30px 0 5px;
  color: #fff;
}
.carousel .card span {
  color: #6A6D78;
  font-size: 1.10rem;
}

@media screen and (max-width: 900px) {
  .carousel {
    grid-auto-columns: calc((100% / 2) - 9px);
  }
}

@media screen and (max-width: 600px) {
  .carousel {
    grid-auto-columns: 100%;
  }
}
</style>