<template>
  <div class="col-xl-3 col-lg-4 col-sm-6 grid-item grid-sizer cat-two">
    <div class="movie-item mb-60">
      <div class="movie-poster">
        <vue-easy-lightbox
            moveDisabled
            :visible="visible"
            :imgs="imgs"
            :index="index"
            @hide="handleHide">

        </vue-easy-lightbox>
        <a href="#" @click.prevent="showMultiple('https://gofra-mahsulotlari.com/storage/uploads/'+action+'/'+items.image)">
            <img :src="'https://gofra-mahsulotlari.com/storage/uploads/'+action+'/'+items.image" alt="">
        </a>
      </div>
      <div class="movie-content">
        <div class="top">
          <h5 class="title"><a href="#">{{ items.name }}</a></h5>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueEasyLightbox from 'vue-easy-lightbox'

export default {
  name: "MovieItem2",
  components: {
    VueEasyLightbox
  },
  props:['items','action'],
  data(){
    return{
      imgs: require('../../img/lightbox/ucm_poster01.jpg'),
      visible: false,
      index: 0
    }
  },
  methods:{
    getImgUrl(pic){
      return require(pic)
    },
    showMultiple() {
      this.imgs = [
        'https://gofra-mahsulotlari.com/storage/uploads/'+this.action+'/'+this.items.image
      ]

      this.index = 0
      this.show()
    },
    show() {
      this.visible = true
    },
    handleHide() {
      this.visible = false
    }
  },
}
</script>

<style scoped>
h5 a{
  text-decoration: none;
}
</style>